@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos{
    color: v.$negro;
    text-align: center;
    font-size: 1.5rem;
    font-weight: 200;
    a{
        font-weight: 900;
        color: v.$logo;
        &:hover{
            transition: 0.5s;
            color: v.$morado;
        }
    }
}
.listado-proyectos{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .proyecto{
        background-color: lighten(v.$secundario, 4%);
        min-height: 4rem;
        display: flex;
        align-items: center;
        padding: 1rem 0rem 1rem 2rem;
        border-radius: .4rem;
        line-height: 1;

        @include m.tablet {
            padding: 2rem;
        }
        
        &:hover{
            background-color: lighten(v.$secundario, 9%);
        }
    }

    a{
        color: v.$blanco;
        font-weight: 700;
    }
}