@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    @include m.tablet{
        display: flex;
        height: 100vh;
    }

    .principal{
        scrollbar-width: none;
        flex: 1;
        overflow-y: scroll;
    }
    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 2.3rem;
            text-align: center;
        }
    }
    .formulario{
        input:not([type="submit"]) {
            border-bottom: 1px solid v.$secundario;
        }
        input::placeholder{
            color: gray;
        }
        input[type="submit"] {
            @include m.boton (v.$secundario, v.$blanco);
        }
    }
    .enlace{
        color: v.$secundario;
        font-weight: 900;
        cursor: pointer;
        &:hover {
            color: lighten(v.$secundario, 18%);
        }
    }
}