@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile{
    background-color: v.$primario;
    padding: 0.8rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet {
        display:none;
    }
    h1{
        margin: 0;
        font-size: 0;
        img{
            width: 8.4rem;
        }
    }
    .menu{
        button{
            color: v.$blanco;
            border: none;
            background-color: #00000030;
            font-size: 1.3rem;
            font-weight: 900;
            padding: 0.7rem;
            border-radius: 0.5rem;
        }
    }
}

.barra{
    display: none;
    @include m.tablet {
        background-color: v.$blanco;
        padding: 0 2rem;
        box-shadow: 1px 12px 9px -12px rgba(0, 0 ,0, .4);
    }
    p{
        color: v.$negro;
        font-size: 1.5rem;
        margin: 0.6rem 0 0 0;
        @include m.tablet {
            margin: 1rem 0; 
        }

        span{
            color: v.$primario;
            font-weight: 900;
        }
    }
    a{
        color: v.$negro;
        font-size: 1.5rem;
        font-weight: 900;
    }
    .cerrar-sesion{
        @include m.boton( darken(v.$secundario, 0%), v.$blanco);
        color: v.$negro;
        padding: 0rem 0rem 1rem 0em;
        font-size: 1.50rem;
        @include m.tablet {
            background-color: #0000;
            padding: 0;
            font-size: 1.6rem;
            &:hover{
                background-color: #0000;
            }
        }
        
    }
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 2rem;
    }
}
