@use 'base/variables' as v;
@use 'base/mixins' as m;

.crear-cuenta{
    @include m.big-desktop {
        padding: 6%;
    }
    .formulario{
        margin: 0 2rem 0 2rem;
        .campo{
            margin-bottom: 1rem;
            font-size: 62.5%;
        }
    }
    .acciones{
        margin-top: 2rem;
        a{   
            margin-bottom: 0;
            padding-bottom: 1rem;
            &:first-of-type{
                padding-bottom: 1rem;
            }
            
        }
        @include m.tablet {
            margin-top: 5rem;
        }
    }
    
}