@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;

    .agregar-tarea{
        @include m.boton(v.$secundario, v.$blanco)
    }
}

.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 0.2rem;
    .no-tareas{
        text-align: center;
    }
}

.tarea{
    padding: .5rem 2rem;
    border-bottom: 2px solid v.$negro;
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:last-of-type{
        border: none;
    }
    p{
        color: v.$negro;
        font-size: 1.4rem;
    }

    .opciones{
        display: flex;
        gap: 1rem;
        button{
            border: none;
            font-size: 1.2rem;
            padding: .7rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 0.2rem;
            &:hover{
                cursor: pointer;
            }
        }
        .completa{
            background-color: v.$indigo;
            color: v.$blanco;
        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
        }
        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
        }
    }
}