@use 'base/variables' as v;
@use 'base/mixins' as m;

.filtros{
    margin-top: 2rem;
    background-color: v.$logo;
    padding: 0.5rem;
    border-radius: 0.2rem;
    @include m.tablet {
        padding: 1rem;
    }

    .filtros-inputs{
        @include m.tablet {
            display: flex;
            align-items: center;
            gap: 2rem;
            justify-content: space-between;
        }
    }
    h2{
        text-align: center;
        color: v.$blanco;;
        font-size: 1.8rem;
        @include m.tablet {
            font-size: 1.9rem;
            margin: 0;
            text-align: left;
        }
    }
    label{
        color: v.$blanco;
    }
    .campos{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        @include m.tablet {
            margin: 0;
        }
    }
    input[type="radio"]{
        accent-color: v.$primario;
        width: 1.8rem;
        height: 1.8rem;
    }
}