@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar{
    background-color: v.$primario;
    opacity: 0;
    height: 0;
    transition-property: opacity;
    transition-duration: 500ms;
    overflow: hidden;
    
    &.mostrar{
        opacity: 1;
        padding: 1rem 2rem 1rem 2rem;
        height: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        @include m.tablet {
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }

    @include m.tablet {
        height: 100%;
        opacity: 1;
    }

    @include m.tablet {
        .boton{
            font-size: 0;
        }
    }

    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include m.tablet {
            justify-content: center;
        }
        .write{
            display: flex;
            justify-content: center;
            margin: 0;
            padding: 0.4rem 0;
            img{
                width: 13rem;
                @include m.tablet {
                    width: 11rem;
                    padding: 1.7rem 0 1rem 0;
                }
            }
        }
        .cerrar-menu{
            button{
                font-size: 2rem;
                color: v.$blanco;
                font-weight: 900;
                border: none;
                background-color: #0000;
                padding: 2rem 1rem 2rem 3rem;
            }
            @include m.tablet {
                display: none;
            }
        }
    }
    
    
    .sidebar-nav{
        margin-top: 21rem;
        @include m.tablet {
            margin-top: 0rem; 
        }
        a{
            color: v.$blanco;
            display: flex;
            justify-content: center;
            font-weight: 900;
            font-size: 1.8rem;
            padding: 1rem 0 1rem 0;
            &.activo{
                background-color: lighten(v.$primario, 20%);
            }
            @include m.tablet {
                justify-content: left;
                padding: 1.5rem 10rem 1.5rem 1.8rem;
                font-size: 1.4rem;
                font-weight: 700;
                margin-top: 0.2rem;
            }

            @include m.tablet {
                transition-property: background-color;
                transition-duration: 300ms;
                &:hover{
                    background-color: lighten(v.$primario, 20%);
                } 
                &.activo{
                    background-color: lighten(v.$primario, 20%);
                    border-left: 3px solid v.$terciario;
                    @include m.desktop {
                        border-left: 6px solid v.$terciario;
                    }
                }
            }
        }
    }

    .cerrar-sesion-mobile{
        position: absolute;
        right: 15%;
        left: 15%;
        bottom: 4rem;
        /* a{
            color: v.$blanco;
            font-weight: 900;
        } */
        .cerrar-sesion{
            @include m.boton(v.$secundario, v.$blanco)
        }


        @include m.tablet {
            display: none;
        }
    }
}
