html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 100%;
  margin: 0rem auto;
  height: 100vh;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  /* .descripcion-pagina{
      font-size: 2rem;
      text-transform: uppercase;
      color: v.$secundario;
  } */
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #ffffff;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #ffffff;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
  font-size: 1.5rem;
  margin: 2rem 3rem 0 3rem;
}

/** Media Queries **/
.formulario {
  margin: 2.8rem 3rem 0 3rem;
}
.formulario .campo {
  margin-bottom: 3rem;
  display: flex;
  flex-direction: column;
}
.formulario label {
  margin-bottom: 1rem;
  font-weight: 200;
  font-size: 1.9rem;
  /* @include m.tablet{
      display: flex;
      justify-content: center;
  } */
}
.formulario input {
  padding: 1rem;
  border-bottom: 2px solid #ffffff;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  background-color: #ffffff00;
  font-size: 1.5rem;
}
.formulario input::placeholder {
  color: #c4c4c4;
  font-size: 1.5rem;
}
.formulario .campo-son:hover {
  border-bottom: 3px solid #3a169b;
  transition: 0.5s;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 900;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.confirmar {
  margin: 30% 3rem 0 2rem;
}
@media (min-width: 768px) {
  .confirmar {
    margin: 10% 0 0 0;
  }
  .confirmar .acciones {
    justify-content: center;
  }
}

.alertas {
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.3rem;
  margin: 0.5rem 10%;
}
@media (min-width: 768px) {
  .alertas {
    margin: 1.5rem 0;
    padding: 0.2rem;
  }
}
.alertas.error {
  background: #a90000;
  color: transparent;
  background-clip: text;
  border-right: 0.3rem solid #a90000;
}
@media (min-width: 768px) {
  .alertas.error {
    border-left: 0.5rem solid #a90000;
    border-right: none;
    margin: 1rem 30%;
    text-align: center;
  }
}
.alertas.exito {
  background: #00de00;
  color: transparent;
  background-clip: text;
  border-right: 0.3rem solid #007800;
}
@media (min-width: 768px) {
  .alertas.exito {
    border-left: 0.5rem solid #007800;
    border-right: none;
    text-align: center;
  }
}

.login {
  background: #1a2e44;
}
@media (min-width: 768px) {
  .login {
    background: linear-gradient(to bottom, #1a2e44 63%, #284769 100%);
  }
}
.login .uptask {
  display: flex;
  justify-content: center;
  width: 100%;
  padding-top: 2.2rem;
  margin: 0;
  padding-bottom: 1.5rem;
}
.login .uptask img {
  width: 12rem;
}
@media (min-width: 768px) {
  .login .uptask img {
    width: 13rem;
  }
}
@media (min-width: 1400px) {
  .login .uptask img {
    width: 20rem;
  }
}
.login .tagline {
  font-size: 2rem;
  font-weight: 200;
  margin: 0 2.5rem;
  color: #ffffff;
}
.login input[type=submit] {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #14387d;
  cursor: pointer;
}

.separacion {
  padding: 20% 0 0 0;
}

@media (min-width: 768px) {
  .separacion {
    padding: 0;
  }
}
@media (min-width: 1400px) {
  .crear-cuenta {
    padding: 6%;
  }
}
.crear-cuenta .formulario {
  margin: 0 2rem 0 2rem;
}
.crear-cuenta .formulario .campo {
  margin-bottom: 1rem;
  font-size: 62.5%;
}
.crear-cuenta .acciones {
  margin-top: 2rem;
}
.crear-cuenta .acciones a {
  margin-bottom: 0;
  padding-bottom: 1rem;
}
.crear-cuenta .acciones a:first-of-type {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .crear-cuenta .acciones {
    margin-top: 5rem;
  }
}

@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  scrollbar-width: none;
  flex: 1;
  overflow-y: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.3rem;
  text-align: center;
}
.dashboard .formulario input:not([type=submit]) {
  border-bottom: 1px solid #0f2a5e;
}
.dashboard .formulario input::placeholder {
  color: gray;
}
.dashboard .formulario input[type=submit] {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #14387d;
  cursor: pointer;
}
.dashboard .enlace {
  color: #0f2a5e;
  font-weight: 900;
  cursor: pointer;
}
.dashboard .enlace:hover {
  color: #1c4dad;
}

.sidebar {
  background-color: #1a2e44;
  opacity: 0;
  height: 0;
  transition-property: opacity;
  transition-duration: 500ms;
  overflow: hidden;
}
.sidebar.mostrar {
  opacity: 1;
  padding: 1rem 2rem 1rem 2rem;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .sidebar .boton {
    font-size: 0;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar {
    justify-content: center;
  }
}
.sidebar .contenedor-sidebar .write {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0.4rem 0;
}
.sidebar .contenedor-sidebar .write img {
  width: 13rem;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .write img {
    width: 11rem;
    padding: 1.7rem 0 1rem 0;
  }
}
.sidebar .contenedor-sidebar .cerrar-menu button {
  font-size: 2rem;
  color: #FFFFFF;
  font-weight: 900;
  border: none;
  background-color: #0000;
  padding: 2rem 1rem 2rem 3rem;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 21rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav {
    margin-top: 0rem;
  }
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  font-weight: 900;
  font-size: 1.8rem;
  padding: 1rem 0 1rem 0;
}
.sidebar .sidebar-nav a.activo {
  background-color: #36608e;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    justify-content: left;
    padding: 1.5rem 10rem 1.5rem 1.8rem;
    font-size: 1.4rem;
    font-weight: 700;
    margin-top: 0.2rem;
  }
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    transition-property: background-color;
    transition-duration: 300ms;
  }
  .sidebar .sidebar-nav a:hover {
    background-color: #36608e;
  }
  .sidebar .sidebar-nav a.activo {
    background-color: #36608e;
    border-left: 3px solid #ffffff;
  }
}
@media (min-width: 768px) and (min-width: 1024px) {
  .sidebar .sidebar-nav a.activo {
    border-left: 6px solid #ffffff;
  }
}
.sidebar .cerrar-sesion-mobile {
  position: absolute;
  right: 15%;
  left: 15%;
  bottom: 4rem;
  /* a{
      color: v.$blanco;
      font-weight: 900;
  } */
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #14387d;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

.barra-mobile {
  background-color: #1a2e44;
  padding: 0.8rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  margin: 0;
  font-size: 0;
}
.barra-mobile h1 img {
  width: 8.4rem;
}
.barra-mobile .menu button {
  color: #FFFFFF;
  border: none;
  background-color: #00000030;
  font-size: 1.3rem;
  font-weight: 900;
  padding: 0.7rem;
  border-radius: 0.5rem;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    padding: 0 2rem;
    box-shadow: 1px 12px 9px -12px rgba(0, 0, 0, 0.4);
  }
}
.barra p {
  color: #000;
  font-size: 1.5rem;
  margin: 0.6rem 0 0 0;
}
@media (min-width: 768px) {
  .barra p {
    margin: 1rem 0;
  }
}
.barra p span {
  color: #1a2e44;
  font-weight: 900;
}
.barra a {
  color: #000;
  font-size: 1.5rem;
  font-weight: 900;
}
.barra .cerrar-sesion {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  color: #000;
  padding: 0rem 0rem 1rem 0em;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #14387d;
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    background-color: #0000;
    padding: 0;
    font-size: 1.6rem;
  }
  .barra .cerrar-sesion:hover {
    background-color: #0000;
  }
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
  }
}

.dashboard .contenedor-sm.crear {
  margin: 34% 0;
}
@media (min-width: 768px) {
  .dashboard .contenedor-sm.crear {
    margin: 24% auto;
  }
}
@media (min-width: 1024px) {
  .dashboard .contenedor-sm.crear {
    margin: 18% auto;
  }
}

.no-proyectos {
  color: #000;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 200;
}
.no-proyectos a {
  font-weight: 900;
  color: #3a169b;
}
.no-proyectos a:hover {
  transition: 0.5s;
  color: #7C3AED;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background-color: #123270;
  min-height: 4rem;
  display: flex;
  align-items: center;
  padding: 1rem 0rem 1rem 2rem;
  border-radius: 0.4rem;
  line-height: 1;
}
@media (min-width: 768px) {
  .listado-proyectos .proyecto {
    padding: 2rem;
  }
}
.listado-proyectos .proyecto:hover {
  background-color: #153c86;
}
.listado-proyectos a {
  color: #FFFFFF;
  font-weight: 700;
}

.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #14387d;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 0.2rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #000;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  color: #000;
  font-size: 1.4rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 1.2rem;
  padding: 0.7rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.2rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completa {
  background-color: #4338CA;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #b46820;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: #a90000;
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.modal .formulario {
  background-color: #FFFFFF;
  padding: 2rem 3rem;
  margin: 0 1.5rem;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 1000ms;
  opacity: 0;
  border-radius: 0.4rem;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario label {
  margin-top: 1rem;
}
.modal .formulario .opciones {
  display: block;
}
@media (min-width: 768px) {
  .modal .formulario .opciones {
    display: flex;
    justify-content: space-between;
  }
}
.modal .formulario .submit-nueva-tarea {
  background-color: #0f2a5e;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-bottom: 0.7rem;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #14387d;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    margin-bottom: 0;
  }
}
.modal .formulario .cerrar-modal {
  background-color: #b46820;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #d27a25;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}
@media (min-width: 768px) {
  .modal .formulario {
    max-width: 60rem;
    margin: 0 auto;
  }
  .modal .formulario.animar {
    top: 35%;
  }
}

.filtros {
  margin-top: 2rem;
  background-color: #3a169b;
  padding: 0.5rem;
  border-radius: 0.2rem;
}
@media (min-width: 768px) {
  .filtros {
    padding: 1rem;
  }
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    display: flex;
    align-items: center;
    gap: 2rem;
    justify-content: space-between;
  }
}
.filtros h2 {
  text-align: center;
  color: #FFFFFF;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .filtros h2 {
    font-size: 1.9rem;
    margin: 0;
    text-align: left;
  }
}
.filtros label {
  color: #FFFFFF;
}
.filtros .campos {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filtros .campos {
    margin: 0;
  }
}
.filtros input[type=radio] {
  accent-color: #1a2e44;
  width: 1.8rem;
  height: 1.8rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }

  .dashboard {
    color: #FFFFFF;
  }
  .dashboard .enlace {
    color: #FFFFFF;
  }
  .dashboard .campo-son {
    color: #FFFFFF;
  }
  .dashboard .formulario legend {
    color: #FFFFFF;
  }
  .dashboard .formulario label {
    color: #FFFFFF;
  }
  .dashboard .formulario input {
    color: #FFFFFF;
  }
  .dashboard .formulario input:not([type=submit]) {
    border-bottom: 1px solid #7C3AED;
  }
  .dashboard .filtros {
    background-color: #424258;
  }
  .dashboard .filtros input[type=radio] {
    accent-color: #b46820;
  }
  .dashboard .barra {
    background-color: #1a2e44;
  }
  .dashboard .barra p {
    color: #FFFFFF;
  }
  .dashboard .barra span {
    color: #b46820;
  }
  .dashboard .cerrar-sesion {
    color: #FFFFFF;
  }
  .dashboard .tarea p {
    color: #FFFFFF;
  }
  .dashboard .formulario.nueva-tarea {
    background-color: #424258;
  }
}