@use 'variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
    min-height: 100%;
    
}
.contenedor {
    width: 100%;
    margin: 0rem auto;
    height: 100vh;
}
.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;

    /* .descripcion-pagina{
        font-size: 2rem;
        text-transform: uppercase;
        color: v.$secundario;
    } */
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
