@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal{
    position: absolute;
    background-color: rgb(0 0 0 /.4);
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    .formulario{
        background-color:v.$blanco;
        padding: 2rem 3rem;
        margin: 0 1.5rem;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 1000ms;
        opacity: 0;
        border-radius: 0.4rem;

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }
        label{
            margin-top: 1rem;
        }
        .opciones{
            display: block;
            @include m.tablet {
                display: flex;
                justify-content: space-between
            }
        }
        .submit-nueva-tarea{
            @include m.boton(v.$secundario, v.$blanco);
            margin-bottom: 0.7rem;
            @include m.tablet {
                margin-bottom: 0;
            }
        }
        .cerrar-modal{
            @include m.boton(v.$naranja, v.$blanco);
        }

        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }

        @include m.tablet {
            max-width: 60rem;
            margin: 0 auto;
            &.animar{
                top: 35%;
            }
        }
    }
}