@use 'base/mixins' as m;
@use 'base/variables' as v;

.alertas{
    
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 1rem;
    font-size: 1.3rem;
    margin: 0.5rem 10%;
    
    @include m.tablet {
        margin: 1.5rem 0;
        padding: 0.2rem;
    }
    &.error{
        background: lighten(v.$rojo, 0%);
        color: transparent;
        background-clip: text;
        border-right: .3rem solid v.$rojo;
        @include m.tablet {
            border-left: .5rem solid v.$rojo; 
            border-right: none;
            margin: 1rem 30%;
            text-align: center;
        }
    }
    &.exito{
        background: lighten(v.$verde, 20%);
        color: transparent;
        background-clip: text;
        border-right: .3rem solid v.$verde;
        @include m.tablet {
            border-left: .5rem solid v.$verde; 
            border-right: none;
            text-align: center;
        }
    }
}