@use 'base/variables' as v;
@use 'base/mixins' as m;

.login{
    background: v.$primario;
    @include m.tablet {
        background: linear-gradient(to bottom, v.$primario 63%, lighten(v.$primario, 10%) 100%);
    }
    .uptask{
        display: flex;
        justify-content: center;
        width: 100%;
        padding-top: 2.2rem;
        margin: 0;
        padding-bottom: 1.5rem;
        img{
            width: 12rem;
            @include m.tablet {
                width: 13rem;
            }
            @include m.big-desktop {
                width: 20rem;
            }
        }
    }

    .tagline{
        font-size: 2rem;
        font-weight: 200;
        margin: 0 2.5rem;
        color: v.$terciario;
    }

    input[type='submit']{
        @include m.boton(v.$secundario, v.$terciario);
        margin-top: 5rem;
    }
}
