@use  'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin: 2.8rem 3rem 0 3rem;

    .campo{
        margin-bottom: 3rem;
        display: flex;
        flex-direction: column;
    }
    label{
        margin-bottom: 1rem;
        font-weight: 200;
        font-size: 1.9rem;
        /* @include m.tablet{
            display: flex;
            justify-content: center;
        } */
    }
    input{
        padding: 1rem;
        border-bottom: 2px solid v.$terciario;
        border-top: none;
        border-left: none;
        border-right: none;
        outline: none;
        background-color: #ffffff00;
        font-size: 1.5rem;
    }
    input::placeholder{
        color: v.$place;
        font-size: 1.5rem;
    }
    .campo-son:hover{
        border-bottom: 3px solid v.$logo;
        transition: 0.5s;
    }
}