@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark) {
    $grisOScuro: #2c2c3b;
    body{
        background-color: $grisOScuro;
    }

    .dashboard{
        color: v.$blanco;
        .enlace{
            color: v.$blanco;
        }
        .campo-son{
            color: v.$blanco;
        }
        .formulario{ 
            legend{
            color: v.$blanco;
            }
            label{
                color: v.$blanco;
            }
        }
        .formulario input{
            color: v.$blanco;
        }
        .formulario input:not([type=submit]){
            border-bottom: 1px solid v.$morado
        }
        .filtros{

            background-color: lighten($grisOScuro, 10%);
            input[type="radio"]{
                accent-color: v.$naranja;
            }
        }
        .barra{
            background-color: v.$primario;
            p{
                color: v.$blanco;            
            }
            span{
                color: v.$naranja;
            }
        }
        .cerrar-sesion{
            color: v.$blanco;
        }
        .tarea{
            p{
                color:v.$blanco;
            }
        }
        .formulario.nueva-tarea{
            background-color: lighten($grisOScuro, 10%);
        }
    }
}